import store from 'store'
import { ref, reactive, computed, provide } from 'vue'
import { isFunction, isObject } from 'utils/is'
import { deepClone } from 'utils/deep-clone'
import { mapTabRoute } from 'utils/mapping'

import { locationLinkedStatus } from 'constants/franchisee'

// injection symbols
export const storeLocationSymbols = {
  state: Symbol(),
  actionAll: Symbol(),
  loading: Symbol()
}

const useSwitchLocationType = () => {
  const data = reactive({
    old_location_id: null,
    name: null,
    street: null,
    city: null,
    zip_code: null,
    state: null,
    country: null,
    phone: null,
    lat: null,
    lon: null,
    email: null,
    address_text: null,
    suite: null
  })
  const modal = ref(false)
  const loading = ref(false)

  const isIncompleteForm = computed(() => {
    const requiredAttributes = ['name', 'email', 'address_text', 'phone']
    const emptyAttributes = Object.entries(data).filter(
      ([key, value]) => requiredAttributes.includes(key) && value === null
    )
    return emptyAttributes.length > 0
  })

  const action = async (callback = () => { }) => {
    try {
      loading.value = true
      await store.dispatch('setting/manageLocation/switchLocation', data)
      isFunction(callback) && callback()
    } finally {
      loading.value = false
    }
  }
  return {
    data,
    modal,
    loading,
    isIncompleteForm,
    action
  }
}

const useStoreLocation = () => {
  const data = reactive({
    page: 1,
    limit: 10,
    search: '',
    sort_by: '',
    order_by: '',
    franchisor_accept_status: {
      name: '',
      value: ''
    }
  })
  const dataLocation = reactive({
    new_store_id: null
  })
  const loading = reactive({
    all: false,
    delete: false,
    mailin_location: false
  })
  const modal = reactive({
    create: false,
    update: false,
    delete: false
  })
  const isShowSwitchLocationModal = ref(false)
  const isShowEnableMailinModal = ref(false)
  const selected = ref(null)
  const tab = computed(() => {
    const items = [
      {
        id: 0,
        name: 'Store Locations',
        route: {
          name: 'settingManageLocationStoreLocations'
        }
      },
      {
        id: 1,
        name: 'Linked Locations',
        route: {
          name: 'settingManageLocationLinkedLocations'
        }
      }
    ].map((item) => mapTabRoute(item, item.route.name, {}, 'id', 'name'))
    return items
  })
  const state = computed(() => {
    return isObject(store.state.setting.manageLocation.all)
      ? deepClone({ ...store.state.setting.manageLocation.all })
      : { data: [] }
  })
  const locationList = computed(() => {
    // const locations = [...(store.state.setting.manageLocation.all?.data  || [])]
    // if (isObject(store.state.setting.manageLocation.all?.mailin_location)) locations.push(store.state.setting.manageLocation.all.mailin_location)
    // return locations
    return store.state.setting.manageLocation.all?.data || []
  })
  const unsetData = () => {
    selected.value = null
    dataLocation.new_store_id = null
  }
  const openModal = (target, callback = () => { }) => {
    modal[target] = true
    isFunction(callback) && callback()
  }
  const actionAll = async (admin = undefined, callback = () => { }) => {
    loading.all = true
    const payload = {
      ...data,
      franchisor_accept_status: data.franchisor_accept_status.value || '',
      ...(admin && { admin_id: admin })
    }
    await store.dispatch('setting/manageLocation/all', payload)
    isFunction(callback) && callback()
    loading.all = false
  }
  const actionDelete = async (callback = () => { }) => {
    try {
      loading.delete = true
      await store.dispatch(`setting/manageLocation/delete`, {
        id: selected.value,
        data: dataLocation
      })
      isFunction(callback) && callback()
      modal.delete = false
      unsetData()
      await actionAll(store.state.user.me.id)
    } finally {
      loading.delete = false
    }
  }
  const actionMailLocation = async (payload, callback = () => { }) => {
    try {
      loading.mailin_location = true
      await store.dispatch(`setting/manageLocation/mailLocation`, payload)
      isFunction(callback) && callback(undefined)
    } catch (err) {
      isFunction(callback) && callback(err)
    } finally {
      loading.mailin_location = false
    }
  }

  provide(storeLocationSymbols.actionAll, actionAll)
  provide(storeLocationSymbols.loading, loading)
  provide(storeLocationSymbols.state, state)

  return {
    data,
    dataLocation,
    state,
    loading,
    modal,
    isShowSwitchLocationModal,
    isShowEnableMailinModal,
    selected,
    tab,
    locationList,
    unsetData,
    openModal,
    actionAll,
    actionDelete,
    actionMailLocation
  }
}

const useFranchiseeStoreLocation = () => {
  const data = reactive({
    page: 1,
    limit: 10,
    search: '',
    sort_by: '',
    order_by: ''
  })
  const loading = reactive({
    all: false
  })

  const actionAll = async (linkedStatus = 1, callback = () => { }) => {
    loading.all = true
    const payload = {
      ...data,
      linked_status: linkedStatus ? linkedStatus : 1
    }
    await store.dispatch('setting/manageLocation/getFranchiseeStore', payload)
    isFunction(callback) && callback()
    loading.all = false
  }
  const state = computed(() => {
    return isObject(store.state.setting.manageLocation.franchisee)
      ? deepClone({ ...store.state.setting.manageLocation.franchisee })
      : { data: [] }
  })

  return {
    data,
    loading,
    state,
    actionAll
  }
}

const useFranchisee = () => {
  const linkedStatuses = ref(locationLinkedStatus)
  const loading = reactive({
    requestFranchisee: new Map(),
    approveFranchisee: new Map(),
    rejectFranchisee: new Map(),
    revokeFranchisee: new Map(),
    cancelFranchisee: new Map(),
    unlinkFranchisee: new Map()
  })
  const actionRequestFranchisee = async (data, callback = () => { }) => {
    try {
      loading.requestFranchisee.set(data.location_id, true)
      const payload = {
        id: data.location_id,
        data: {
          franchisor_code: data.franchisor_code
        }
      }
      await store.dispatch(`setting/manageLocation/requestFranchisee`, payload)
      isFunction(callback) && callback()
    } finally {
      loading.requestFranchisee.delete(data.location_id)
    }
  }
  const actionApproveFranchisee = async (id, callback = () => { }) => {
    try {
      loading.approveFranchisee.set(id, true)
      const response = await store.dispatch(
        `setting/manageLocation/approveFranchisee`,
        id
      )
      isFunction(callback) && callback(response)
    } finally {
      loading.approveFranchisee.delete(id)
    }
  }
  const actionRejectFranchisee = async (id, callback = () => { }) => {
    try {
      loading.rejectFranchisee.set(id, true)
      const response = await store.dispatch(
        `setting/manageLocation/rejectFranchisee`,
        id
      )
      isFunction(callback) && callback(response)
    } finally {
      loading.rejectFranchisee.delete(id)
    }
  }
  const actionRevokeFranchisee = async (id, callback = () => { }) => {
    try {
      loading.revokeFranchisee.set(id, true)
      const response = await store.dispatch(
        `setting/manageLocation/revokeFranchisee`,
        id
      )
      isFunction(callback) && callback(response)
    } finally {
      loading.revokeFranchisee.delete(id)
    }
  }
  const actionCancelFranchisee = async (id, callback = () => { }) => {
    try {
      loading.cancelFranchisee.set(id, true)
      const response = await store.dispatch(
        `setting/manageLocation/cancelFranchisee`,
        id
      )
      isFunction(callback) && callback(response)
    } finally {
      loading.cancelFranchisee.delete(id)
    }
  }
  const actionUnlinkFranchisee = async (id, callback = () => { }) => {
    try {
      loading.unlinkFranchisee.set(id, true)
      const response = await store.dispatch(
        `setting/manageLocation/unlinkFranchisee`,
        id
      )
      isFunction(callback) && callback(response)
    } finally {
      loading.unlinkFranchisee.delete(id)
    }
  }
  return {
    linkedStatuses,
    loading,
    actionRequestFranchisee,
    actionApproveFranchisee,
    actionRejectFranchisee,
    actionRevokeFranchisee,
    actionCancelFranchisee,
    actionUnlinkFranchisee
  }
}

const useFranchiseeLocationWidget = () => {
  const loading = reactive({
    show: false,
    update: false
  })
  const data = reactive({
    id: null,
    widget_app_type: null
  })
  const resetData = () => {
    Object.assign(data, {
      id: null,
      widget_app_type: null
    })
  }
  const actionUpdate = async (callback = () => { }) => {
    try {
      loading.update = true
      const { id, ...restData } = data
      const response = await store.dispatch(
        'setting/manageLocation/widgetCodeFranchisee',
        {
          id,
          data: {
            ...restData
          }
        }
      )
      isFunction(callback) && callback(response)
    } finally {
      loading.update = false
    }
  }

  return {
    loading,
    data,
    resetData,
    actionUpdate
  }
}

export {
  useStoreLocation,
  useFranchiseeStoreLocation,
  useFranchisee,
  useFranchiseeLocationWidget,
  useSwitchLocationType
}


export const useAdditionalPrice = () => {
  const loading = ref(false)
  const chargedProrateAmount = ref(null)
  const refundedProrateAmount = ref(null)
  const prorateBillingCycleType = ref('monthly')

  const billingCycleType = computed(() => prorateBillingCycleType.value?.toLowerCase?.())

  const getChargedProrateAmount = async () => {
    try {
      loading.value = true
      const response = await store.dispatch('setting/manageLocation/getProrateAmount', {
        type: 'add'
      })
      const { total: totalChargedAmount, interval: billingCycle } = (response || {})
      chargedProrateAmount.value = totalChargedAmount
      prorateBillingCycleType.value = billingCycle
    } catch (err) {
      console.warn(`Error while getting charged prorate price amount: `, err.message)
    } finally {
      loading.value = false
    }
  }
  const getRefundedProrateAmount = async () => {
    try {
      loading.value = true
      const response = await store.dispatch('setting/manageLocation/getProrateAmount', {
        type: 'remove'
      })
      const { total: totalRefundedAmount, interval: billingCycle } = (response || {})
      refundedProrateAmount.value = totalRefundedAmount
      prorateBillingCycleType.value = billingCycle
    } catch (err) {
      console.warn(`Error while getting refunded prorate price amount: `, err.message)
    } finally {
      loading.value = false
    }
  }
  return {
    loading,
    chargedProrateAmount,
    refundedProrateAmount,
    billingCycleType,
    getChargedProrateAmount,
    getRefundedProrateAmount
  }
}